import './head.scss';
import $ from 'zepto';
import logo from './logo.png';

export default {
    name: 'head',
    data() {
        return {
            logo: logo,
            scrollTop: 0,
            hide: false
        };
    },
    methods: {
        handleScroll: function (e) {
            if (!this.hide) {
                if ($("html").scrollTop() >= 50) {
                    this.hide = true;
                }
            }

            if (this.hide) {
                if ($("html").scrollTop() < 50) {
                    this.hide = false;
                }
            }
        }
    },
    mounted: function () {
        window.addEventListener('scroll', this.handleScroll);
    }

};